header {
  background: #01474a;
}

.admin-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
}

.admin-wrapper .left-sidebar {
  -ms-flex: 2 1;
      flex: 2 1;
  height: 100%;
  background: #722571;
  margin-top: -5px;
}

.admin-wrapper .left-sidebar ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
  color: #ddd;
}

.admin-wrapper .left-sidebar ul li a {
  padding: 15px;
  display: block;
  border-bottom: 1px solid #ddd;
}

.admin-wrapper .left-sidebar ul li a:hover {
  background: #521751;
  color: #fa923f;
}

.admin-wrapper .admin-content {
  -ms-flex: 8 1;
      flex: 8 1;
  height: 100%;
  padding: 40px 100px;
  overflow-y: scroll;
}

.admin-content .page-title {
  text-align: center;
  margin-bottom: 1.5rem;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 1.1rem;
}

#myInput {
  font-size: 15px;
  padding: 5px;
}

th,
td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #d3d3d3;
}

.edit {
  color: green;
}

.delete {
  color: red;
}

.publish {
  color: blue;
}

.edit:hover,
.delete:hover,
.publish:hover {
  text-decoration: underline;
}

form div {
  margin-bottom: 15px;
}

.text-input {
  padding: 0.7rem 1rem;
  display: block;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #e0e0e0;
  outline: none;
  color: #444;
  line-height: 1.5rem;
  font-size: 1.2em;
  font-family: "Lora", serif;
}



