@import url("https://fonts.googleapis.com/css?family=Poppins&display=swap");

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
}
.box {
  width: 450px;
  background: #3b46ac;
  padding: 40px;
  text-align: center;
  margin: auto;
  color: #fff;
  font-family: "Poppins", sans-serif;
  margin-top: 6%;
  border-radius: 5px;
}

.profile_info {
  background: #521751;
  padding: 10px 5px;
}

.profile_info.form {
  font-size: 16px;
}

.profile_info input[type="text"],
.profile_info input[type="email"],
.profile_info input[type="date"] {
  border: 0;
  background: none;
  display: block;
  margin: 0px auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 7px 10px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}

.box h1 {
  font-size: 40px;
  letter-spacing: 4px;
  font-weight: 100;
}

.box h3 {
  letter-spacing: 4px;
  font-weight: 100;
  text-align: center;
}

.box p {
  text-align: center;
}

.box-img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
}

.profile_info-edit {
  padding: 5px 20px;
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 0;
  color: #fff;
  background: #521751;
  cursor: pointer;
  border: none;
}

.profile_info-update {
  padding: 5px 20px;
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 0;
  color: #fff;
  background: #3b46ac;
  cursor: pointer;
  border: none;
  border-radius: 3px;
}

.profile_info-update:hover {
  background: #242b69;
}

@media only screen and (max-width: 600px) {
  .box {
    width: 400px;
    margin-top: 10%;
  }
  .box-img {
    width: 150px;
    height: 150px;
  }
  .box h3 {
    font-size: 15px;
  }
}
@media only screen and (max-width: 500px) {
  .box {
    width: 300px;
  }
}

@media only screen and (max-width: 400px) {
  .box {
    width: 100%;
    margin-top: none;
    padding: 5px;
  }
  .box-img {
    width: 100px;
    height: 100px;
  }
  .box h1 {
    font-size: inherit;
  }
  .box h3 {
    font-size: 15px;
  }
}



